export class DynamoDBToolboxError extends Error {
    constructor(code, { message, path, payload }) {
        super(message);
        this.code = code;
        this.path = path;
        this.payload = payload;
    }
}
DynamoDBToolboxError.match = (error, prefix = '') => error instanceof DynamoDBToolboxError && error.code.startsWith(prefix);
